/*#region Grids*/
.Album {
  display: grid;
  grid-template-areas: "Title" "Photo" "Download" "Buttons";
}

.Title {
  grid-area: Title;
}

.Photo {
  grid-area: Photo;
  width: 100%;
}

.Download {
  grid-area: Download;
}

.Buttons {
  grid-area: Buttons;
  display: grid;

  grid-template-areas: "PrevsButton NextButton";
}

.PrevButton {
  grid-area: PrevsButton;
}

.NextButton {
  grid-area: NextButton;
}
/*#endregion*/

/*#region Loader*/
.loader {
  margin: 10px;
  border: 1rem solid #0c0032;
  border-top: 1rem solid #3500d3;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  animation: spin 2s both infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/*#endregion*/

/*#region Galery*/
.gallery {
  border: 1px solid #ccc;
}

.gallery:hover {
  border: 1px solid #777;
}

.gallery img {
  width: 100%;
  height: auto;
}

.desc {
  padding: 15px;
  text-align: center;
}

* {
  box-sizing: border-box;
}

.responsive {
  padding: 0 6px;
  float: left;
  width: 24.99999%;
}

@media only screen and (max-width: 700px) {
  .responsive {
    width: 49.99999%;
    margin: 6px 0;
  }
}

@media only screen and (max-width: 500px) {
  .responsive {
    width: 100%;
  }
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}
/*#endregion*/
